fieldset {
  width: 35rem;
  height: 27rem;
  margin: 10rem auto;
  line-height: 3rem;
}
input {
  height: 2rem;
}
textarea {
  height: 15rem;
}
.form-wrapper {
  display: flex;
  flex-direction: column;
}
label {
 display: flex;
 flex-direction: column;
}
button {
  background-color: gray;
  border: none;
  outline: none;
  padding: .5em .8em;
}
button:hover,
button:active,
button:focus {
  background-color: rgb(88, 87, 87);
  color: #fff;
}

.loading {
  display: inline-block;
  width: 11px;
  height: 11px;
  border: solid 3px rgb(88, 87, 87);
  border-radius: 50%;
  border-top-color: rgb(61, 2, 2);
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
  margin-left: 10px;
}

@keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}

@-webkit-keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}